//  s = f(t)
// (x,y)tt-xs+y
//  k tt/ks*k
// s = (f((t/k)-x)+y)*k
// s = (f((t-x)/k)*k)+y

const baseEase = {
  linear: t => t,
  inQuad: t => t * t,
  outQuad: t => 1 - (1 - t) * (1 - t),
  inCubic: t => t * t * t,
  outCubic: t => 1 - (1 - t) * (1 - t) * (1 - t),
  inQuart: t => t * t * t * t,
  outQuart: t => 1 - (1 - t) * (1 - t) * (1 - t) * (1 - t),
  inQuint: t => t * t * t * t * t,
  outQuint: t => 1 - (1 - t) * (1 - t) * (1 - t) * (1 - t) * (1 - t)
};
// easeFneasingab
export function createEase(a = 0, b = 1, easeFn = baseEase.linear) {
  const k = 1 / (b - a);
  if (!k || k <= 0) {
    return () => 0;
  }
  // easeFn( -a , -easeFn(a) ) k 
  // (0,1)1
  return t => (easeFn(t / k + a) - easeFn(a)) * k / ((easeFn(1 / k + a) - easeFn(a)) * k);
}
// easingeasingk
export function createEaseInOut(k = 1 / 2, easeIn = baseEase.linear, easeOut = baseEase.linear) {
  if (k <= 0) {
    return easeOut;
  }
  if (k >= 1) {
    return easeIn;
  }
  return t => t <= k ?
  // easeInk
  easeIn(t / k) * k :
  // easeOuteaseIn( k/(1-k) , k/(1-k) ) 1-k
  (easeOut(t / (1 - k) - k / (1 - k)) + k / (1 - k)) * (1 - k);
}
const easeInOutQuad = createEaseInOut(1 / 2, baseEase.outQuad, baseEase.inQuad);
const easeOutInQuad = createEaseInOut(1 / 2, baseEase.inQuad, baseEase.outQuad);
const easeOutQuad = baseEase.outQuad;
const easeOutQuart = baseEase.outQuart;
export { easeInOutQuad, easeOutInQuad, easeOutQuad, easeOutQuart };