import type Gesture from '@huangjs888/gesture';
import Transition, { TAProperty, easeOutQuart, type EaseFn } from '@huangjs888/transition';
import loadImage, { type Image, type ImageOption } from './image';
import bindGesture from './events';
import {
  createContainer,
  createBackdrop,
  createIndicator,
  createSubstance,
  createItemIndicator,
  createItemWrapper,
  setStyle,
} from './dom';
import { popupComputedSize, popupTransform, type RectSize } from './popup';

class Gallery {
  _container: HTMLElement | null = null;
  _substance: HTMLElement | null = null;
  _backdrop: HTMLElement | null = null;
  _indicator: HTMLElement | null = null;
  _itemGap: number = 0;
  _direction: Direction = 'horizontal';
  _rectSize: RectSize | null = null; // 
  _isClose: boolean = true;
  _swipeClose: boolean = false;
  _closeDestory: boolean = false;
  _originRect: RectSize | null = null;
  _press: (() => void) | null = null;
  _longPress: (() => void) | null = null;
  _onChange: (() => void) | null = null;
  _onImageEnd: ((i: number, o: boolean) => void) | null = null;
  _removeResize: (() => void) | null = null;
  _images: Image[] | null = null;
  _activeIndex: number = 0;
  _translate: number = 0; // swiper
  _transition: Transition | null = null; // 
  _gesture: Gesture | null = null; // 
  _fgBehavior: number = 0; // 0:  1:  2: 
  _moveTarget: 'closures' | 'outside' | 'inside' | 'none' = 'none'; // swiper
  constructor({
    container: ele,
    imageUrls = [],
    activeIndex = 0,
    direction = 'horizontal',
    itemGap = 20,
    hasLoading = true,
    hasIndicator = true,
    isLazy = true,
    swipeClose = true,
    closeDestory = true,
    backdropColor = '#000f',
    originRect,
    press,
    longPress,
    onChange,
    onResize,
    onImageEnd,
    options = {},
  }: SOption) {
    const container = (this._container = createContainer(ele));
    this._backdrop = createBackdrop(backdropColor, container);
    const substance = (this._substance = createSubstance(direction === 'vertical', container));
    const indicator = (this._indicator = createIndicator(
      direction === 'vertical',
      hasIndicator && imageUrls.length > 1,
      container,
    ));
    const gesture = (this._gesture = bindGesture.apply(this, [container]));
    this._images = imageUrls.map((url, index) => {
      const image: Image = {
        wrapper: createItemWrapper(
          index === 0,
          direction === 'vertical',
          hasLoading,
          itemGap,
          substance,
        ),
        indicator: createItemIndicator(direction === 'vertical', indicator),
        url,
        width: 0,
        height: 0,
        options: {
          rotation: !gesture.isTouch() ? [-Number.MAX_VALUE, Number.MAX_VALUE] : undefined,
          scalation: !gesture.isTouch() ? [0.1, 10] : undefined,
          ...options,
        },
      };
      if (!isLazy) {
        // show
        loadImage(image).then((okay) => {
          if (okay) {
            this.resetItemSize(index);
          }
          if (typeof okay === 'boolean' && typeof this._onImageEnd === 'function') {
            this._onImageEnd(index, okay);
          }
        });
      }
      return image;
    });
    // 
    this._transition = new Transition({
      element: substance,
      propertyName: 'transform',
      propertyValue: new (class extends TAProperty {
        toString() {
          return `translate${direction === 'vertical' ? 'Y' : 'X'}(${this.value.translate}px)`;
        }
      })({ translate: this._translate }),
    });
    this._itemGap = itemGap;
    this._direction = direction;
    this._swipeClose = swipeClose;
    this._closeDestory = closeDestory;
    this._originRect = originRect || null;
    this._press = press || null;
    this._longPress = longPress || null;
    this._onChange = onChange || null;
    this._onImageEnd = onImageEnd || null;
    this._activeIndex = activeIndex;
    // 
    const resize = () => {
      this.resetSize();
      this.resetItemSize();
      typeof onResize === 'function' && onResize();
    };
    const contextmenu = (e: Event) => {
      e.preventDefault();
      e.stopImmediatePropagation();
    };
    window.addEventListener('resize', resize);
    window.addEventListener('contextmenu', contextmenu);
    this._removeResize = () => {
      window.removeEventListener('resize', resize);
      window.removeEventListener('contextmenu', contextmenu);
    };
  }
  resetSize() {
    if (!this._container || !this._substance || !this._images) {
      return;
    }
    // 
    const { left, top, width, height } = this._container.getBoundingClientRect();
    this._rectSize = { left, top, width, height };
    const length = this._images.length;
    if (length > 0) {
      setStyle(this._substance, {
        width:
          this._direction === 'vertical' ? width : width * length + (length - 1) * this._itemGap,
        height:
          this._direction === 'vertical' ? height * length + (length - 1) * this._itemGap : height,
      });
    }
  }
  resetItemSize(index?: number) {
    if (!this._rectSize || !this._images) {
      return;
    }
    const { left, top, width, height } = this._rectSize;
    const resize = (image: Image) => {
      if (image && image.entity && width !== 0 && height !== 0) {
        image.entity.setSizeInfo({
          containerCenter: [left + width / 2, top + height / 2],
          containerWidth: width,
          containerHeight: height,
          naturalWidth: image.width,
          naturalHeight: image.height,
        });
        const { elementHeight } = image.entity.getSizeInfo();
        if (this._direction !== 'vertical' && elementHeight > height) {
          setStyle(image.wrapper, {
            alignItems: 'flex-start',
          });
        }
      }
    };
    if (typeof index === 'number') {
      resize(this._images[index]);
    } else {
      this._images.forEach(resize);
    }
  }
  getItemSize() {
    if (!this._rectSize) {
      return 0;
    }
    const { width, height } = this._rectSize;
    const wh = this._direction === 'vertical' ? height : width;
    return wh === 0 ? 0 : wh + this._itemGap;
  }
  slide(
    index: number,
    options?: {
      duration?: number;
      easing?: EaseFn;
    },
    open: boolean = false,
  ) {
    if (!this._images || this._images.length === 0) {
      return Promise.resolve();
    }
    const _index = Math.max(Math.min(index, this._images.length - 1), 0);
    const isChange = open || this._activeIndex !== _index;
    if (isChange) {
      const { indicator: lastOne } = this._images[this._activeIndex] || {};
      if (lastOne) {
        setStyle(lastOne, {
          width: 7,
          height: 7,
          opacity: 0.6,
        });
      }
      const { indicator: thisOne } = this._images[_index] || {};
      if (thisOne) {
        setStyle(thisOne, {
          width: 8,
          height: 8,
          opacity: 1,
        });
      }
      this._activeIndex = _index;
      loadImage(this._images[_index]).then(
        // lazy
        (okay) => {
          if (okay) {
            this.resetItemSize(_index);
          }
          if (typeof okay === 'boolean' && typeof this._onImageEnd === 'function') {
            this._onImageEnd(_index, okay);
          }
        },
      );
    }
    const _translate = -_index * this.getItemSize();
    return this.transitionRun(_translate, options).then((v) => {
      if (isChange && typeof this._onChange === 'function') {
        this._onChange();
      }
      return v;
    });
  }
  next(options?: { duration?: number; easing?: EaseFn }) {
    return this.slide(this._activeIndex + 1, options);
  }
  prev(options?: { duration?: number; easing?: EaseFn }) {
    return this.slide(this._activeIndex - 1, options);
  }
  transitionRun(
    translate: number,
    options: {
      duration?: number;
      easing?: EaseFn;
      before?: EaseFn;
    } = {},
  ) {
    const delta = translate - this._translate;
    if (delta === 0 || !this._transition) {
      return Promise.resolve({ translate: this._translate });
    }
    if (typeof options.duration === 'number' && options.duration <= 0) {
      // 
      this._translate = translate;
      this._transition.bind({ translate });
      return Promise.resolve({ translate });
    }
    this._translate = translate;
    return this._transition
      .apply(
        { translate: delta },
        {
          precision: { translate: 1 },
          cancel: true,
          duration: 500,
          easing: easeOutQuart,
          ...options,
        },
      )
      .then((value) => {
        if (!this.isTransitioning()) {
          // _transitionvalue_transform
          if (this._transition) {
            this._transition.bind({ translate: this._translate });
          }
        }
        return value;
      });
  }
  transitionCancel() {
    if (!this._transition) {
      return 0;
    }
    // cancel
    return this._transition.cancel().map((value) => {
      // this._transform
      this._translate -= value.translate;
    }).length;
  }
  isTransitioning() {
    if (!this._transition) {
      return false;
    }
    return this._transition.transitioning();
  }
  destory() {
    // 
    if (this._gesture) {
      this._gesture.destory();
      this._gesture = null;
    }
    if (this._removeResize) {
      this._removeResize();
      this._removeResize = null;
    }
    this._isClose = true;
    this._transition = null;
    this._rectSize = null;
    this._originRect = null;
    this._images = null;
    this._press = null;
    this._longPress = null;
    this._onChange = null;
    this._substance = null;
    this._backdrop = null;
    this._indicator = null;
    if (this._container && this._container.parentNode) {
      this._container.parentNode.removeChild(this._container);
      this._container = null;
    }
  }
  setSwipeClose(swipeClose: boolean = true) {
    if (!this._container) {
      return;
    }
    this._swipeClose = swipeClose;
  }
  setCloseDestory(closeDestory: boolean = true) {
    if (!this._container) {
      return;
    }
    this._closeDestory = closeDestory;
  }
  setOriginRect(originRect: RectSize | null = null) {
    if (!this._container) {
      return;
    }
    this._originRect = originRect;
  }
  open() {
    if (!this._container || !this._isClose) {
      return;
    }
    this._isClose = false;
    const { _backdrop: backdrop, _container: container } = this;
    setStyle(container, {
      display: 'block',
    });
    // 
    this.resetSize();
    // openresetItemSize
    // container
    // container
    this.resetItemSize();
    this.slide(this._activeIndex, { duration: 0 }, true);
    const { wrapper = null, entity } = (this._images && this._images[this._activeIndex]) || {};
    let elementSize: RectSize | null = null;
    let elementEl: HTMLElement | null = null;
    if (entity) {
      const size = entity.getSizeInfo();
      elementEl = entity.getElement();
      elementSize = {
        width: size.elementWidth,
        height: size.elementHeight,
        top: 0,
        left: 0,
      };
    }
    const { x, y, k, w, h } = popupComputedSize(this._originRect, this._rectSize, elementSize);
    popupTransform({ el: backdrop, o: 0 }, { el: wrapper, x, y, k }, { el: elementEl, w, h });
    // popupTransformpopupTransform
    container.getBoundingClientRect();
    popupTransform(
      { el: backdrop, o: 1 },
      { el: wrapper, x: 0, y: 0, k: 1 },
      {
        el: elementEl,
        ...(elementSize ? { w: elementSize.width, h: elementSize.height } : {}),
      },
      300,
    ).then(() => {
      if (this._indicator) {
        setStyle(this._indicator, {
          display: 'flex',
        });
      }
    });
  }
  close() {
    if (!this._container || this._isClose) {
      return;
    }
    this._isClose = true;
    const { wrapper = null, entity } = (this._images && this._images[this._activeIndex]) || {};
    let elementSize: RectSize | null = null;
    let elementEl: HTMLElement | null = null;
    if (entity) {
      const size = entity.getSizeInfo();
      elementEl = entity.getElement();
      elementSize = {
        width: size.elementWidth,
        height: size.elementHeight,
        top: 0,
        left: 0,
      };
      // 
      entity.reset();
    }
    if (this._indicator) {
      setStyle(this._indicator, {
        display: 'none',
      });
    }
    const { x, y, k, w, h } = popupComputedSize(this._originRect, this._rectSize, elementSize);
    popupTransform(
      { el: this._backdrop, o: 0 },
      { el: wrapper, x, y, k },
      { el: elementEl, w, h },
      300,
    ).then(() => {
      if (this._closeDestory) {
        this.destory();
      } else if (this._container) {
        setStyle(this._container, {
          display: 'none',
        });
      }
    });
  }
}

export type Direction = 'vertical' | 'horizontal';

export type SOption = {
  container?: HTMLElement | string; // 
  imageUrls?: string[]; // imageurl
  direction?: Direction; // 
  activeIndex?: number; // 
  itemGap?: number; // 
  backdropColor?: string; // backdroprgba(0,0,0,0.9)
  hasLoading?: boolean; // loading
  hasIndicator?: boolean; // 
  isLazy?: boolean; // 
  originRect?: RectSize; // 
  swipeClose?: boolean; // 
  closeDestory?: boolean; // hide
  options?: ImageOption; // 
  press?: () => void; // singleTap
  longPress?: () => void; // longTap
  onChange?: () => void; // 
  onResize?: () => void; // 
  onImageEnd?: (i: number, o: boolean) => void; // 
};

export default Gallery;
